.overview-container {
	max-width: 100%
}

.overview-container .project-overview {
	padding: 1rem
}

.overview-container .project-overview a {
	text-decoration: none;
	color: #fff
}

.overview-container .project-overview h2 {
	margin-top: 0;
	margin-bottom: .5rem
}

.overview-container .project-overview figure {
	position: relative;
	overflow: hidden
}

.overview-container .project-overview figure img {
	transition: -webkit-transform .3s ease;
	transition: transform .3s ease;
	transition: transform .3s ease, -webkit-transform .3s ease
}

.overview-container .project-overview figure img:hover {
	-webkit-transform: scale(1.03) translateY(0);
	transform: scale(1.03) translateY(0);
	cursor: pointer
}

.overview-container .project-overview figure .project-description {
	position: absolute;
	width: 100%;
	left: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.822);
	-webkit-backdrop-filter: blur(5px);
	backdrop-filter: blur(5px)
}

.overview-container .project-overview figure .project-description h2 {
	margin: .5rem;
	line-height: 1.5rem
}

.overview-container .project-overview figure .project-description p {
	margin: 0 .5rem .5rem .5rem
}

.overview-container .project-overview figure:hover>.project-description {
	background-color: rgba(0, 0, 0, 0.822);
	-webkit-backdrop-filter: blur(5px);
	backdrop-filter: blur(5px)
}

.overview-container .project-overview figure .appear-on-hover {
	display: none
}

.overview-container .project-overview figure:hover>div>.appear-on-hover {
	display: block
}

.overview-container .project-overview .featured {
	grid-column: 1 / -1
}

.single-project-container {
	max-width: 100%;
	margin: 0 auto
}

.single-project-container .project-single {
	padding: 1rem
}

.single-project-container .project-single div {
	margin: 0 auto;
	max-width: 1500px;
	position: relative
}

.single-project-container .project-single div h2 {
	display: inline-block;
	font-size: 3rem;
	margin: 0 0 .5rem 0
}

.single-project-container .project-single div video {
	max-width: 100%;
	height: auto;
	cursor: pointer;
	margin-bottom: 1rem
}

.single-project-container .project-single div .project-info-important {
	background-color: #032533;
	color: #fff;
	height: -webkit-min-content;
	height: min-content
}

.single-project-container .project-single div .project-info-important h3 {
	font-size: 1.5rem;
	margin: 1rem 0;
	padding: 0 1rem
}

.single-project-container .project-single div p {
	font-size: 1.3rem;
	margin: 0
}

.single-project-container #back-btn {
	position: absolute;
	top: 0;
	right: 0
}

.single-project-container #project-btn {
	padding: 1rem 1rem 0 1rem
}

html {
	scroll-behavior: smooth
}

body {
	position: relative;
	font-family: "Quicksand", sans-serif;
	background-color: #374a53;
	color: #fff;
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	font-size: 16px
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	font-weight: 400;
	line-height: 1.5
}

img {
	display: block
}

figure {
	margin: 0;
	padding: 0
}

.App {
	margin: 0 auto
}

.has-two-col {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 1rem
}

.has-two-col-1-3 {
	display: grid;
	grid-template-columns: 340px 6fr
}

.text-center {
	text-align: center
}

#btn-grid {
	display: grid;
	grid-template-columns: 1fr 1fr
}

img {
	width: 100%;
	height: auto
}

.btn-group {
	display: grid;
	grid-auto-columns: -webkit-min-content;
	grid-auto-columns: min-content;
	grid-auto-flow: column;
	text-decoration: none;
	gap: 1rem
}

.btn-group .btn {
	display: inline-block;
	margin: 0;
	padding: .8rem 2rem;
	border: 0;
	text-decoration: none;
	cursor: pointer;
	white-space: nowrap;
	font-size: 1rem;
	text-align: center;
	width: -webkit-min-content;
	width: min-content
}

.btn-group .btn.align-left {
	margin-right: auto
}

.btn-group .btn.align-center {
	margin-left: auto;
	margin-right: auto
}

.btn-group .btn.align-right {
	margin-left: auto
}

.btn-group .btn.has-primary-text {
	color: #032533
}

.btn-group .btn.has-primary-bg-color {
	background-color: #032533
}

.btn-group .btn.has-primary-bg-color:hover {
	background-color: black
}

.btn-group .btn.has-primary-outline-color {
	border: 2px solid #032533;
	color: #032533
}

.btn-group .btn.has-primary-outline-color:hover {
	border: 2px solid
}

.btn-group .btn.has-secondary-text {
	color: #374a53
}

.btn-group .btn.has-secondary-bg-color {
	background-color: #374a53
}

.btn-group .btn.has-secondary-bg-color:hover {
	background-color: #0e1316
}

.btn-group .btn.has-secondary-outline-color {
	border: 2px solid #374a53;
	color: #374a53
}

.btn-group .btn.has-secondary-outline-color:hover {
	border: 2px solid
}

.btn-group .btn.has-light-text {
	color: #fff
}

.btn-group .btn.has-light-bg-color {
	background-color: #fff
}

.btn-group .btn.has-light-bg-color:hover {
	background-color: #ccc
}

.btn-group .btn.has-light-outline-color {
	border: 2px solid #fff;
	color: #fff
}

.btn-group .btn.has-light-outline-color:hover {
	border: 2px solid
}

.btn-group .btn.has-black-text {
	color: black
}

.btn-group .btn.has-black-bg-color {
	background-color: black
}

.btn-group .btn.has-black-bg-color:hover {
	background-color: black
}

.btn-group .btn.has-black-outline-color {
	border: 2px solid black;
	color: black
}

.btn-group .btn.has-black-outline-color:hover {
	border: 2px solid
}

.about-me-slider-track {
	background-color: #032533;
	color: #fff
}

.about-me-slider {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	height: 100vh;
	width: 340px
}

.about-me-slider .about-me {
	padding: 1rem
}

.about-me-slider .about-me nav {
	text-align: right;
	margin: 0
}

.about-me-slider .about-me img {
	margin: 3rem auto 0 auto;
	width: 300px;
	height: 300px;
	border-radius: 50%
}

.about-me-slider .about-me h1 {
	font-size: 2rem;
	margin-top: 1rem;
	margin-bottom: 0
}

.about-me-slider .about-me p {
	font-size: 1.2rem;
	margin-top: 1rem;
	margin-bottom: 0
}

.about-me-slider .about-me a {
	font-size: 1.2rem;
	display: block;
	color: #fff;
	text-decoration: underline
}

@media(max-width:1024px) {
	.single-project-container .project-single div h2 {
		font-size: 2rem
	}

	.has-two-col {
		grid-template-columns: 1fr
	}
}

@media(max-width:768px) {
	.overview-container .project-overview figure .appear-on-hover {
		display: block
	}

	.overview-container .project-overview figure .appear-on-hover.hide-on-mobile {
		display: none
	}

	.overview-container .project-overview figure:hover>div>.appear-on-hover.hide-on-mobile {
		display: none
	}

	.has-two-col-1-3 {
		grid-template-columns: 1fr
	}

	.btn-group .btn.align-right,
	.btn-group .btn.align-left {
		margin-left: auto;
		margin-right: auto
	}

	.about-me-slider {
		position: relative;
		height: auto;
		margin: 1rem auto
	}

	.about-me-slider .about-me img {
		margin: 1rem auto 0 auto
	}
}

@media(max-width:425px) {
	.single-project-container #back-btn {
		display: none
	}
}